.main-header {
  width: 100%;
  height: 80px;
  background-color: #00a82d;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-size: 28px;
}

.exercises__list-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 0;
}

.exercises__list-item-right {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.exercises__card {
  margin-top: 20px;
}

.exercises__checkbox {
  margin-right: 5px;
}

.exercises__checkbox--crossouted {
  text-decoration: line-through;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  color: #24292f;
}

ul {
  padding: 0;
  margin: 0;
}

main {
  width: 100%;
}

.container {
  max-width: 100%;
  padding-left: 60px;
  padding-right: 60px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

